<template>
<div class="page page-with-subnavbar page-calendar">
  <div class="fixed-header">
    <slot name="navbar">
    <div class="navbar">  
        <div class="left abs" @click="navBack($event)" >
            <a href="#" class="back_arrow link">&lsaquo;</a></div>
        <div class="title text-center">请选择日期</div>    
    </div>
    </slot> 
    <div class="weeks">
          <div class="calendar-week-day" v-for="(week,w) in weeks" :key="w">{{week}}</div>
    </div>
    
  </div>
  <div class="page-content">
  <div :class="['calendar',range ? 'calendar-range' : '']"> 
    <div class=" calendar-months" >
      <div class="calendar-month" v-for="(item,index) in renderMonths" :key="index">
        <div class="calendar-row text-center">{{`${item.year}年${item.month}月`}}</div>
        <div class="calendar-row" v-for="(day,k1) in item.days" :key="k1">
          <div :class="{'calendar-day':true,'calendar-day-selected':child.selected,'calendar-day-disabled':child.disabled,'null':child.null}"
               v-for="(child,k2) in day" :key="k2" @click="select(item.year,item.month,child,$event)">
            <span :class="{'calendar-day-weekend':k2==0||k2==6||((child.isLunarFestival||child.isGregorianFestival) && lunar)}" >{{child.null?'' : child.day}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
  </div>
</div>
</template>

<script>
  import calendar from '../modules/calendar.js'
  export default {
    props: {
      // 多选模式
      multi: {
        type: Boolean,
        default: false
      },
      // 范围模式
      range:{
        type: Boolean,
        default: false
      },
      // 默认日期
      value: {
        type: Array,
        default: function(){
          return [new Date().getFullYear(),new Date().getMonth()+1,new Date().getDate()]
        }
      },
      // 开始选择日期
      begin:  {
        type: Array,
        default: function(){          
          return [new Date().getFullYear(),new Date().getMonth()+1,new Date().getDate()]
        }
      },
      // 结束选择日期
      end:  {
        type: Array,
        default: function(){
          return []
        }
      },
      //渲染的月数
      count:  {
        type: Number,
        default: function(){
          //如果存在begin和end,从开始选择的日期begin的当月开始算起，默认到结束日期end之间的月数
          if(this.begin.length !=0 && this.end.length !=0){
            let stepDays = (new Date(this.end).getTime()-new Date(this.begin).getTime())/3600000/24;
            return Math.ceil(stepDays/30)+1
          }else if(this.range || this.multi){
            return (this.value[1][1]-this.value[0][1]+1)
          }else {
            return 3;
          }

        }
      },
      // 是否小于10补零
      zero:{
        type: Boolean,
        default: false
      },
      // 屏蔽的日期
      disabled:{
        type: Array,
        default: function(){
          return []
        }
      },
      // 是否显示农历
      lunar: {
        type: Boolean,
        default: false
      },

      // 自定义星期名称
      weeks: {
        type: Array,
        default:function(){
          return window.navigator.language.toLowerCase() == "zh-cn"?['日', '一', '二', '三', '四', '五', '六']:['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat']
        }
      },
      // 自定义月份
      months:{
        type: Array,
        default:function(){
          return window.navigator.language.toLowerCase() == "zh-cn"?['一月', '二月', '三月', '四月', '五月', '六月', '七月', '八月', '九月', '十月', '十一月', '十二月']:['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December']
        }
      },
      // 自定义事件
      events:  {
        type: Object,
        default: function(){
          return {}
        }
      },
    },
    data() {
      return {
        years:[],
        yearsShow:false,
        year: 0,
        month: 0,
        day: 0,
        days: [],
        multiDays:[],
        today: [],
        festival:{
          lunar:{
            "1-1":"春节",
            "1-15":"元宵节",
            "2-2":"龙头节",
            "5-5":"端午节",
            "7-7":"七夕节",
            "7-15":"中元节",
            "8-15":"中秋节",
            "9-9":"重阳节",
            "10-1":"寒衣节",
            "10-15":"下元节",
            "12-8":"腊八节",
            "12-23":"祭灶节",
          },
          gregorian:{
            "1-1":"元旦",
            "2-14":"情人节",
            "3-8":"妇女节",
            "3-12":"植树节",
            "4-5":"清明节",
            "5-1":"劳动节",
            "5-4":"青年节",
            "6-1":"儿童节",
            "7-1":"建党节",
            "8-1":"建军节",
            "9-10":"教师节",
            "10-1":"国庆节",
            "12-24":"平安夜",
            "12-25":"圣诞节",
          },
        },
        rangeBegin:[],
        rangeEnd:[],
        renderMonths: [],
      }
    },
    watch:{
      events(){
        this.render(this.year,this.month)
      },
      value(){
        this.init();
      }
    },
    mounted() {
      this.init();

    },
    methods: {
      init(){
        let now = new Date();
        this.year = now.getFullYear()
        this.month = now.getMonth()
        this.day = now.getDate()
        if (this.value.length>0) {
          if(this.range){//范围
            this.rangeBegin = this.value[0]
            this.rangeEnd = this.value[1]
            if(this.begin.length >0) {
              this.getRenderMonths(this.count,this.begin);
            }else{
              this.getRenderMonths(this.count,this.value[0]);
            }

          }else if(this.multi){//多选
            this.multiDays=this.value;
            this.year = parseInt(this.value[0][0])
            this.month = parseInt(this.value[0][1])
            this.day = parseInt(this.value[0][2])
            this.getRenderMonths(this.count,this.value[0]);
          }else{//单选
            this.year = parseInt(this.value[0])
            this.month = parseInt(this.value[1])
            this.day = parseInt(this.value[2])
            this.getRenderMonths(this.count,this.value);
          }

        }

        //this.render(this.year, this.month)
      },
      getRenderMonths(c,b){
        let now = new Date();
        let beginDate
        if(b != 'undefined' ){
          beginDate =  new Date(b);
        }else{
          beginDate = now;
        }
        let beginMonth = beginDate.getMonth();
        let beginYear = beginDate.getFullYear();
        let renderMonths = [];
        for(let i = 0; i<c; i++){
          let renderYear = beginYear+Math.floor((beginMonth+i)/12);
          let renderMonth = beginMonth+i<12?(beginMonth+i):(beginMonth+i)%12;
          let _days =this.render(renderYear,renderMonth);
          renderMonths.push(Object.assign({days: _days,year: renderYear,month: (renderMonth+1)}) );
        }
        this.renderMonths = renderMonths;
        return renderMonths;
      },

      // 渲染日期 这里面的m是从0计数的
      render(y, m) {
        let firstDayOfMonth = new Date(y, m, 1).getDay()         //当月第一天
        let lastDateOfMonth = new Date(y, m + 1, 0).getDate()    //当月最后一天
        let lastDayOfLastMonth = new Date(y, m, 0).getDate()     //最后一月的最后一天
        let seletSplit = this.value
        let i, line = 0,temp = [],nextMonthPushDays = 1
        for (i = 1; i <= lastDateOfMonth; i++) {
          let day = new Date(y, m, i).getDay() //返回星期几（0～6）
          let k
          // 第一行
          if (day == 0) {
            temp[line] = []
          } else if (i == 1) {
            temp[line] = []
            k = lastDayOfLastMonth - firstDayOfMonth + 1
            for (let j = 0; j < firstDayOfMonth; j++) {
              // console.log("第一行",lunarYear,lunarMonth,lunarValue,lunarInfo)
              temp[line].push(Object.assign(
                {day: k,disabled: true,null: true},
                this.getLunarInfo(this.computedPrevYear(),this.computedPrevMonth(true),k),
                this.getEvents(this.computedPrevYear(),this.computedPrevMonth(true),k),
              ))
              k++;
            }
          }


          if (this.range) {
            // console.log("日期范围",this.getLunarInfo(this.year,this.month+1,i))
            let options = Object.assign(
              {day: i},
              this.getLunarInfo(y, m + 1, i),
              this.getEvents(y, m + 1, i),
            )
            if (this.rangeBegin.length > 0) {
              let beginTime = Number(new Date(this.rangeBegin[0], this.rangeBegin[1] - 1, this.rangeBegin[2]))
              let endTime = Number(new Date(this.rangeEnd[0], this.rangeEnd[1] - 1, this.rangeEnd[2]))
              let stepTime = Number(new Date(y, m, i))
              if (beginTime <= stepTime && endTime >= stepTime) {
                options.selected = true
              }
            }
            if (this.begin.length > 0) {

              let beginTime = Number(new Date(parseInt(this.begin[0]), parseInt(this.begin[1]) - 1, parseInt(this.begin[2])))
              if (beginTime > Number(new Date(y, m, i))) options.disabled = true
            }
            if (this.end.length > 0) {
              let endTime = Number(new Date(parseInt(this.end[0]), parseInt(this.end[1]) - 1, parseInt(this.end[2])))
              if (endTime < Number(new Date(y, m, i))) options.disabled = true
            }
            if (this.disabled.length > 0) {
              if (this.disabled.filter(v => {
                  return y === v[0] && m === v[1] - 1 && i === v[2]
                }).length > 0) {
                options.disabled = true
              }
            }
            temp[line].push(options)
          }else if(this.multi){//多选
            let options
            // 判断是否选中
            if(this.value.filter(v => {return y === v[0] && m === v[1]-1&& i === v[2] }).length>0 ){
              options = Object.assign({day: i,selected:true},this.getLunarInfo(y,m+1,i),this.getEvents(y,m+1,i))
            }else{
              options = Object.assign({day: i,selected:false},this.getLunarInfo(y,m+1,i),this.getEvents(y,m+1,i))
              if (this.begin.length>0) {
                let beginTime = Number(new Date(parseInt(this.begin[0]),parseInt(this.begin[1]) - 1,parseInt(this.begin[2])))
                if (beginTime > Number(new Date(y, m, i))) options.disabled = true
              }
              if (this.end.length>0){
                let endTime = Number(new Date(parseInt(this.end[0]),parseInt(this.end[1]) - 1,parseInt(this.end[2])))
                if (endTime <  Number(new Date(y, m, i))) options.disabled = true
              }
              if (this.disabled.length>0){
                if (this.disabled.filter(v => {return y === v[0] && m === v[1]-1 && i === v[2] }).length>0) {
                  options.disabled = true
                }
              }
            }

            temp[line].push(options)
          }else { // 单选
            // console.log(this.lunar(this.year,this.month,i));

            let chk = new Date()
            let chkY = chk.getFullYear()
            let chkM = chk.getMonth()
            // 匹配上次选中的日期
            if (parseInt(seletSplit[0]) == y && parseInt(seletSplit[1]) - 1 == m && parseInt(seletSplit[2]) == i) {
              // console.log("匹配上次选中的日期",lunarYear,lunarMonth,lunarValue,lunarInfo)
              temp[line].push(Object.assign(
                {day: i,selected: true},
                this.getLunarInfo(y,m+1,i),
                this.getEvents(y,m+1,i),
              ))
              this.today = [line, temp[line].length - 1]
            }
            // 没有默认值的时候显示选中今天日期
            else if (chkY == y && chkM == m && i == this.day && this.value == "") {

              // console.log("今天",lunarYear,lunarMonth,lunarValue,lunarInfo)
              temp[line].push(Object.assign(
                {day: i,selected: true},
                this.getLunarInfo(y,m+1,i),
                this.getEvents(y,m+1,i),
              ))
              this.today = [line, temp[line].length - 1]
            }else{
              // 普通日期
              // console.log("设置可选范围",i,lunarYear,lunarMonth,lunarValue,lunarInfo)
              let options = Object.assign(
                {day: i,selected:false},
                this.getLunarInfo(y,m+1,i),
                this.getEvents(y,m+1,i),
              )
              if (this.begin.length>0) {
                let beginTime = Number(new Date(parseInt(this.begin[0]),parseInt(this.begin[1]) - 1,parseInt(this.begin[2])))
                if (beginTime > Number(new Date(y, m, i))) options.disabled = true
              }
              if (this.end.length>0){
                let endTime = Number(new Date(parseInt(this.end[0]),parseInt(this.end[1]) - 1,parseInt(this.end[2])))
                if (endTime <  Number(new Date(y, m, i))) options.disabled = true
              }
              if (this.disabled.length>0){
                if (this.disabled.filter(v => {return y === v[0] && m === v[1]-1 && i === v[2] }).length>0) {
                  options.disabled = true
                }
              }
              temp[line].push(options)
            }
          }
          // 到周六换行
          if (day == 6 && i < lastDateOfMonth) {
            line++
          }else if (i == lastDateOfMonth) {
            // line++
            let k = 1
            for (let d=day; d < 6; d++) {
              // console.log(this.computedNextYear()+"-"+this.computedNextMonth(true)+"-"+k)
              temp[line].push(Object.assign(
                {day: k,disabled: true,null: true},
                this.getLunarInfo(this.computedNextYear(),this.computedNextMonth(true),k),
                this.getEvents(this.computedNextYear(),this.computedNextMonth(true),k),
              ))
              k++
            }
            // 下个月除了补充的前几天开始的日期
            nextMonthPushDays=k
          }
        } //end for


        this.days = temp;
        return temp;
      },

      computedPrevYear(){
        let value=this.year
        if(this.month-1<0){
          value--
        }
        return value
      },
      computedPrevMonth(isString){
        let value=this.month
        if(this.month-1<0){
          value=11
        }else{
          value--
        }
        // 用于显示目的（一般月份是从0开始的）
        if(isString){
          return value+1
        }
        return value
      },
      computedNextYear(){
        let value=this.year
        if(this.month+1>11){
          value++
        }
        return value
      },
      computedNextMonth(isString){
        let value=this.month
        if(this.month+1>11){
          value=0
        }else{
          value++
        }
        // 用于显示目的（一般月份是从0开始的）
        if(isString){
          return value+1
        }
        return value
      },
      // 获取农历信息
      getLunarInfo(y,m,d){
        let lunarInfo=calendar.solar2lunar(y,m,d)
        let lunarValue=lunarInfo.IDayCn
        let isLunarFestival=false
        let isGregorianFestival=false
        if(this.festival.lunar[lunarInfo.lMonth+"-"+lunarInfo.lDay]!=undefined){
          lunarValue=this.festival.lunar[lunarInfo.lMonth+"-"+lunarInfo.lDay]
          isLunarFestival=true
        }else if(this.festival.gregorian[m+"-"+d]!=undefined){
          lunarValue=this.festival.gregorian[m+"-"+d]
          isGregorianFestival=true
        }
        return {
          lunar:lunarValue,
          isLunarFestival:isLunarFestival,
          isGregorianFestival:isGregorianFestival,
        }
      },
      // 获取自定义事件
      getEvents(y,m,d){
        if(Object.keys(this.events).length==0)return false;
        let eventName=this.events[y+"-"+m+"-"+d]
        let data={}
        if(eventName!=undefined){
          data.eventName=eventName
        }
        return data
      },
      // 上月
      prev(e) {
        e.stopPropagation()
        if (this.month == 0) {
          this.month = 11
          this.year = parseInt(this.year) - 1
        } else {
          this.month = parseInt(this.month) - 1
        }
        this.render(this.year, this.month)
        this.$emit('selectMonth',this.month+1,this.year)
        this.$emit('prev',this.month+1,this.year)
      },
      //  下月
      next(e) {
        e.stopPropagation()
        if (this.month == 11) {
          this.month = 0
          this.year = parseInt(this.year) + 1
        } else {
          this.month = parseInt(this.month) + 1
        }
        this.render(this.year, this.month)
        this.$emit('selectMonth',this.month+1,this.year)
        this.$emit('next',this.month+1,this.year)
      },
      // 选中日期，注意这个m是从1计数的
      select(y,m,child,e) {
        if (e != undefined) e.stopPropagation()
        // 日期范围
        if (this.range) {
          if (this.rangeBegin.length == 0 || this.rangeEndTemp != 0) {
            this.rangeBegin = [y, m,child.day]
            this.rangeBeginTemp = this.rangeBegin
            this.rangeEnd = [y, m,child.day]
            this.rangeEndTemp = 0

          } else {
            this.rangeEnd = [y,m,child.day]
            this.rangeEndTemp = 1
            // 判断结束日期小于开始日期则自动颠倒过来
            if (+new Date(this.rangeEnd[0], this.rangeEnd[1]-1, this.rangeEnd[2]) < +new Date(this.rangeBegin[0], this.rangeBegin[1]-1, this.rangeBegin[2])) {
              this.rangeBegin = this.rangeEnd
              this.rangeEnd = this.rangeBeginTemp
            }
            // 小于10左边打补丁
            let begin=[]
            let end=[]
            if(this.zero){
              this.rangeBegin.forEach((v,k)=>{
                if(k==1)v=v+1
                begin.push(this.zeroPad(v))
              })
              this.rangeEnd.forEach((v,k)=>{
                if(k==1)v=v+1
                end.push(this.zeroPad(v))
              })
            }else{
              begin=this.rangeBegin
              end=this.rangeEnd
            }
            // console.log("选中日期",begin,end)
            this.$emit('select',begin,end)
          }

          this.getRenderMonths(this.count,this.begin);
          // this.render(this.year, this.month)
        }else if (this.multi) {
          // 如果已经选过则过滤掉
          let filterDay=this.multiDays.filter(v => {
            return y === v[0] && m === v[1] && child.day === v[2]
          })
          if( filterDay.length>0 ){
            this.multiDays=this.multiDays.filter(v=> {
              return y !== v[0] || m !== v[1] || child.day !== v[2]
            })
          }else{
            this.multiDays.push([y,m,child.day]);
          }
          child.selected = !child.selected;
          this.$emit('select',this.multiDays)
        } else {
          // 取消上次选中
          if (this.today.length > 0) {
            this.days.forEach(v=>{
              v.forEach(vv=>{
                vv.selected= false
              })
            })
          }
          // 设置当前选中天
          child.selected = true
          this.day =  child.day
          //this.today =  child.day
          this.$emit('select',[y,this.zero?this.zeroPad(m + 1):(m + 1),this.zero?this.zeroPad(child.day):child.day])
        }
      },
      navBack(e) {
        if(e!= undefined) e.stopPropagation();
        this.$emit('navBack')
      },
      changeYear(){
        if(this.yearsShow){
          this.yearsShow=false
          return false
        }
        this.yearsShow=true
        this.years=[];
        for(let i=~~this.year-10;i<~~this.year+10;i++){
          this.years.push(i)
        }
      },
      selectYear(value){
        this.yearsShow=false
        this.year=value
        this.render(this.year,this.month)
        this.$emit('selectYear',value)
      },
      // 返回今天
      setToday(){
        let now = new Date();
        this.year = now.getFullYear()
        this.month = now.getMonth()
        this.day = now.getDate()
        this.render(this.year,this.month)
        // 遍历当前日找到选中
        this.days.forEach(v => {
          let day=v.find(vv => {
            return vv.day==this.day
          })
          if(day!=undefined){
            day.selected=true
          }

        })


        this.$forceUpdate()
      },
      // 日期补零
      zeroPad(n){
        return String(n < 10 ? '0' + n : n)
      },
    }
  }

</script>
<style type="text/css">
    @import url(../assets/css/calendar.css);
</style>




